body {
  margin: 0;
  padding: 0;
  background-color: rgb(33, 37, 43);
  color: white;
}

.highlight-code {
  background-color: black;
}

.span {
  word-wrap: 'break-word';
  white-space: 'pre';
}

/* UI tree root*/
.tree-view {
  width: 1000px;
  overflow-x: hidden;
}

.simplebar-track.vertical {
  top: 0;
  width: auto;
  width: 11px;
}

#code-panel {
  font-family: Consolas, "Courier New", monospace;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px;
  position: fixed;
  /* border styles */
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-left-style: solid;
  border-color: black;
  border-width: 1px;
  margin: 0px 0px 0px 12px;
}

#qual-name-panel {
  font-family: Consolas, "Courier New", monospace;
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
  z-index: 99999999;
  position: fixed;
  background-color:rgba(0, 0, 0, 0.5);
}

#file-name-panel {
  font-family: Consolas, "Courier New", monospace;
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
  z-index: 99999999;
  position: fixed;
  background-color:rgba(0, 0, 0, 0.5);
}

#status-panel {
  margin-left: 0px;
  height: 13px;
  width: 100%;
  font-family: Consolas, "Courier New", monospace;
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
  z-index: 99999999;
  position: fixed;
  background-color:rgba(0, 0, 0, 0.5);
}

#graph-panel {
  position: fixed;
  /* border styles */
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-left-style: solid;
  border-top-style: solid;
  border-color: black;
  border-width: 1px;
  margin: 0px 0px 0px 12px;
}

#rootlist-panel {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 12px;
  border-left-style: solid;
  border-color: black;
  border-width: 1px;
  padding-left: 10px;
  margin: 0px;
}

#right-panel {
  margin: 0px;
}

#alloc-panel {
  font-family: 'Lucida Grande', sans-serif;
  font-size: 11px;
  margin: 0px 0px 0px 30px;
}

.file-name {
  color: #9cdcfe;
}

.prim-obj-val {
  color: lightgrey /*#ce9178*/;
}

.type {
  color: #569cd6;
}

.size-info {
  color: #569cd6;
}

.jsType {
  color: darkgrey /* #9cdcfe */;
}

.line, .column {
  color: #9cdcfe;
}

.function-name, .well-known-token {
  color: #ff9a56;
}

.common-name {
  color: #9cdcfe
}

.prim-value {
  color: #dcdcaa;
}

.prop-name {
  color: #00fbca; /*#00da7b*/
}

.meta-prop-name {
  color: #569cd6;
}

.string-value {
  color: #ce9178;
}

.prim-name {
  color: #569cd6;
}

/* the tree node's style */
.tree-view {
  overflow-y: hidden;
  /*
  border-left-width: 1px;
  border-color: black;
  border-left-style: dotted;
  */
}

.tree-view_item {
  /* immediate child of .tree-view, for styling convenience */
}

/* style for the children nodes container */
.tree-view_children {
  margin-left: 3px;
  padding-left: 13px;
  border-left-style: dotted;
  border-left-width: 1px;
  border-left-color: #404040;
}

.tree-view_children-collapsed {
  height: 0px;
}

.tree-view_arrow {
  cursor: pointer;
  margin-right: 6px;
  display: inline-block;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tree-view_arrow:after {
  content: 'â–¾';
}

/* rotate the triangle to close it */
.tree-view_arrow-collapsed {
  -webkit-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
}

.node {
  -o-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  border-radius: 3px;
}

.node:hover {
  background-color: black;
  cursor: pointer;
}

.expand-content-num {
  font-family: Consolas, "Courier New", monospace;
  font-weight: normal;
  font-size: 12px;
  color: #00fbca;
}

.expand-button {
  -o-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  border-radius: 3px;
  font-family: Consolas, "Courier New", monospace;
  font-weight: normal;
  font-size: 13px;
}

.expand-content {
  color: #569cd6;
  font-family: Consolas, "Courier New", monospace;
  font-weight: normal;
  font-size: 13px;
}

.expand-button:hover {
  background-color: black;
  cursor: pointer;
}

.info, .node {
  padding: 2px 10px 2px 5px;
  font: 12px consolas, sans-serif;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tree-view_arrow {
  -o-transition: all 0.1s;
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
}

.tree-view_arrow-empty {
  color: yellow;
}



