.node {
  stroke: #fff;
  stroke-width: 1px;
}

.node .selected {
  stroke: black;
}

.link {
  stroke: #999;
}

.brush .extent {
  fill-opacity: 0;
  stroke: #fff;
  /* shape-rendering: crispEdges; */
}

.arrow {
	stroke-width: 1;
}

.arrowHead {
    fill: #fff;
}


